.program-card {
  border-radius: 10px;
  position: relative;
  min-height: 585px;
  width: 385px;
  display: flex;
  flex-direction: column;
  justify-content: end;
  align-items: center;
}
@media (max-width: 374px) {
  .program-card {
    min-width: 278px;
    min-height: 392px;
  }
}
.program-card__pic {
  position: absolute;
  height: 585px;
  top: 0;
  left: 0;
  border-radius: 10px;
  background-color: #011140;
}
@media (max-width: 767px) {
  .program-card__pic {
    min-width: 278px;
    min-height: 392px;
  }
}
@media (max-width: 374px) {
  .program-card__pic {
    max-width: 278px;
    max-height: 391px;
    left: 5%;
  }
}
.program-card__pic img {
  border-radius: 50px;
  height: 100%;
  object-fit: cover;
  align-self: center;
}
@media (max-width: 767px) {
  .program-card__pic img {
    min-width: 278px;
    min-height: 392px;
    width: auto;
  }
}
@media (max-width: 374px) {
  .program-card__pic img {
    max-width: 278px;
    min-height: 391px;
  }
}
.program-card__btn {
  position: relative;
}
@media (hover: hover) {
  .program-card:hover:not(.focus-visible) .btn {
