@media (max-width: 374px) {
  .program-list .program-card:nth-child(1) .btn {
    padding: 10px 0;
  }
}
.program-list .program-card:nth-child(2) .btn {
  padding: 25px 110px;
}
@media (max-width: 374px) {
  .program-list .program-card:nth-child(2) .btn {
    padding: 12px 78px;
  }
}
.program-list .program-card:nth-child(4) .btn {
  padding: 25px 70px;
}
@media (max-width: 374px) {
  .program-list .program-card:nth-child(4) .btn {
    padding: 7px 16px;
  }
}

